body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

h1 {
  text-align: center;
}

form {
  background-color: #fff;
  border-radius: 8px;
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

.formfield {
  margin-bottom: 20px;
}

label {
  font-weight: bold;
  display: block;
}

textarea {
  resize: vertical;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  margin-top: 5px;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  transition: background-color .3s;
}

button:hover {
  background-color: #0056b3;
}
